<template>
  <div class="x-upload">
    <upload-dragger v-if="drag" :accept="accept" @file="startUploadFiles"></upload-dragger>

    <template v-else>
      <div class="x-upload-btn" @click="handleClick">
        <slot></slot>
      </div>
      <input type="file" :name="name" :accept="accept" :multiple="multiple" @change="handleChange" ref="inputRef" />
    </template>

    <div class="x-upload-tips">
      <slot name="tip"></slot>
    </div>

    <ul>
      <li v-for="file in uploadFiles" :key="file.uid">
        <div class="list-item">
          <x-icon icon="attachment"></x-icon>
          <!-- <img :src="file.url" alt=""> -->
          <a :href="file.url" target="__blank">{{file.name}}</a>
          <x-icon icon="close-circle" @click="handleRemove(file)"></x-icon>
          <x-progress type="bar" v-if="file.status === UPLOAD_STATUS.UPLOADING" :percentage="file.percentage"></x-progress>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import UploadDragger from './upload-dragger.vue'
import ajax from './ajax.js';
export default {
  name: 'XUpload',
  components: {
    UploadDragger
  },
  props: {
    action: {
      type: String,
      required: true
    },
    fileList: {   // 
      type: Array,
      default () {
        return [];
      },
    },
    name: {
      type: String,
      default: 'file'
    },
    accept: {
      type: String,
      default: ''
    },
    multiple: {
      type: Boolean,
      default: false
    },
    limit: Number,
    onChange: Function,
    onExceed: Function,
    beforeUpload: Function,
    onProgress: Function,
    onSuccess: Function,
    onError: Function,
    beforeRemove: Function,
    onRemove: Function,
    httpRequest: {
      type: Function,
      default: ajax,
    },
    drag: {
      type: Boolean,
      default: false
    },
  },
  data () {
    return {
      uploadFiles: [],  // 
      tempIndex: 1,
      reqs: [],
      UPLOAD_STATUS: {
        READY: 'ready',
        UPLOADING: 'uploading',
        SUCCESS: 'success',
        FAIL: 'fail',
      },
    }
  },
  watch: {
    fileList: {
      immediate: true,
      handler (fileList) {
        this.uploadFiles = fileList.map((item) => {
          item.uid = item.uid || (Date.now() + this.tempIndex++);
          item.status = item.status || 'succss';
          return item
        })
      }
    }
  },
  methods: {
    handleClick () {
      // 
      this.$refs.inputRef.value = null
      this.$refs.inputRef.click()
    },
    handleChange (e) {
      // 
      const files = e.target.files
      if (!files) return
      this.startUploadFiles(files)
    },
    startUploadFiles (files) {
      // 
      if (this.limit && files.length + this.fileList.length > this.limit) {
        this.onExceed && this.onExceed(files, this.fileList)
        return
      }
      let postFiles = [...files]
      if (!this.multiple) postFiles = postFiles.slice(0, 1)
      if (postFiles.length === 0) return;
      postFiles.forEach(rawFile => {
        //  
        this.handleStart(rawFile) // 
        this.upload(rawFile) // 
      })
    },
    handleStart (rawFile) {
      rawFile.uid = Math.random() + this.tempIndex++;  // 
      const file = {
        uid: rawFile.uid,
        status: 'ready', // 
        percentage: 0, // 
        name: rawFile.name,
        size: rawFile.size,
        raw: rawFile
      }
      file.url = URL.createObjectURL(rawFile);
      this.uploadFiles.push(file) // 
      this.onChange && this.onChange(file)
    },
    upload (rawFile) {
      // 
      if (!this.beforeUpload) {
        return this.post(rawFile)
      }
      const flag = this.beforeUpload(rawFile)
      if (flag) {
        this.post(rawFile)
      } else {
        this.uploadFiles.splice(this.uploadFiles.indexOf(this.getFile(rawFile)), 1)
        this.onRemove && this.onRemove(null, rawFile);
      }
      // flag && this.post(rawFile)
    },
    post (rawFile) {
      // , httpRequest
      const { uid } = rawFile; // ajax
      const options = {
        file: rawFile, // 
        filename: this.name,
        action: this.action,
        onProgress: ev => {
          // 
          this.handleProgress(ev, rawFile)
        },
        onSuccess: res => {
          // 
          this.handleSuccess(res, rawFile)
          delete this.reqs[uid];
        },
        onError: err => {
          // 
          this.handleError(err, rawFile)
          delete this.reqs[uid];
        }
      }
      const req = this.httpRequest(options);
      this.reqs[uid] = req; // ajax
      // promiseajax
      if (req && req.then) {
        req.then(options.onSuccess, options.onError)
      }
    },
    getFile (rawFile) {
      return this.uploadFiles.find(file => file.uid == rawFile.uid);
    },
    handleProgress (ev, rawFile) {
      const file = this.getFile(rawFile)   //   -> 
      file.status = this.UPLOAD_STATUS.UPLOADING;
      file.percentage = ev.percent || 0 // 
      this.onProgress(ev, file, this.uploadFiles);// 
    },
    handleSuccess (res, rawFile) {
      const file = this.getFile(rawFile)
      file.status = this.UPLOAD_STATUS.SUCCESS
      file.response = res
      this.onSuccess(res, file, this.uploadFiles)
      this.onChange(file, this.uploadFiles)
    },
    handleError (err, rawFile) {
      const file = this.getFile(rawFile)
      file.status = this.UPLOAD_STATUS.FAIL
      this.uploadFiles.splice(this.uploadFiles.indexOf(file), 1)
      this.onError(err, file, this.uploadFiles)
      this.onChange(file, this.uploadFiles)
    },
    handleRemove (file, rawFile) {
      if (rawFile) {
        file = this.getFile(rawFile);
      }
      const doRemove = () => {
        this.abort(file);
        this.uploadFiles.splice(this.uploadFiles.indexOf(file), 1);
        this.onRemove && this.onRemove(file, this.uploadFiles);
      }
      if (!this.beforeRemove) {
        doRemove();
      }
    },
    abort (file) {
      const { reqs } = this;
      let uid = file;
      if (file.uid) uid = file.uid;
      if (reqs[uid]) {
        reqs[uid].abort();
      }
    },
  },

}
</script>
<style lang="scss" scoped>
@import "../style/var.scss";
.x-upload {
  input {
    display: none;
  }
  &-btn {
    display: inline-block;
  }
  &-tips {
    margin: 12px 0;
    color: #9b9393;
    font-size: 14px;
  }
  ul {
    list-style: none;
    .list-item {
      margin: 4px;
      .x-icon {
        &:first-of-type {
          fill: $primaryColor !important;
          margin-right: 5px;
          width: 18px !important;
        }
        &:last-of-type {
          cursor: pointer;
          fill: $dangerColor !important;
          margin-left: 5px;
          width: 16px !important;
        }
      }
      a {
        text-decoration: none;
        color: #606266;
        &:hover {
          color: $primaryColor;
        }
      }
    }
  }
}
</style>
