//
import baseImg from '@/assets/logo.png'

let timer = null
//
let observer = new IntersectionObserver(entries => {
  //entries
  entries.forEach(entry => {
    if (entry.isIntersecting || entry.intersectionRatio > 0) {
      //
      !entry.target.isLoaded && showImage(entry.target, entry.target.data_src)
    }
  })
})
function showImage(el, imgSrc) {
  const img = new Image()
  img.src = imgSrc
  img.onload = () => {
    el.src = imgSrc
    el.isLoaded = true
  }
}
export default {
  name: 'lazy',
  //insertedbindIntersectionObserverinserted
  //insertedbindinserteddom
  inserted(el, binding, vnode) {
    clearTimeout(timer) //
    el.src = baseImg //dom
    el.data_src = binding.value
    observer.observe(el) //

    const vm = vnode.context
    timer = setTimeout(() => {
      vm.$on('hook:beforeDestroy', () => {
        observer.disconnect()
      })
    }, 20)
  }, //
  update(el, binding) {
    el.isLoaded = false
    el.data_src = binding.value
  } //unbindbeforeDestroy //unbind(){ //// //observer.disconnect(); //}
}
