  border: 2px dashed $primaryColor;
  display: flex;
  flex-direction: row;
  padding: 10px;
  align-items: center;
  .icon {
    width: 46px;
    height: 46px;
    margin: 0 23px;
    fill: $primaryColor;
  }
}
</style>
